@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Poppins:wght@200;300;400;500;600;700;800;900&family=Roboto:wght@400;500;700;900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.container{
  width: 100%;
  height: 100vh;
  background-color: purple;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box{
  padding: 15px 22px;
    background: white;
    border-radius: 5px;
    width: 300px;
}

h1{
  text-align: center;
  position: relative;
  font-size: 27px;
  margin-bottom: 25px;
}

h1::after{
  content: '';
  width: 15%;
  height: 5px;
  background-color: purple;
  border-radius: 5px;
  position: absolute;
  bottom: -8px;
  right: 41%;
}

.input-boxes{
  margin-top: 20px;
  margin-bottom: 10px;
}

.input{
  margin: 10px 0px;
  padding: 10px 8px;
  background-color: #cbcbcb;
  width: 100%;
  display: flex;
}

.input input{
  border: none;
  background: transparent;
  margin-left: 8px;
  width: 90%;
}

.input input:focus{
  border: none;
  outline: none;
  background: transparent;
}

p{
  font-size: 13px;
}

span{
  color: blue;
  cursor: pointer;
}

.buttons{
  margin: 15px 0;
  display: flex;
  justify-content: center;
}

.icon{
  color: gray;
  width: 10%;
}

.submit {
  padding: 7px 35px;
  background: purple;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  margin: 0px 5px;
  cursor: pointer;
}

.gray{
  background-color: rgb(140, 140, 140);
}